import React, {Component} from 'react';
import {Grid, Container, Typography} from "@material-ui/core"
import './App.css';
import GitHubIcon from '@material-ui/icons/GitHub';

export default class About extends Component {
    constructor(props) {
        super(props);
        this.state = {}
    }
    render() {
        return (
            <Grid 
            container
            xs={12} 
            >
                <Grid xs={12}>
                    <Typography variant="h5">{this.props.title}</Typography>
                    {this.gitHubLink()}
                </Grid>
                <Grid md={4} xs={12} className="projectSection">
                    <Typography variant="h6" align="center">{this.props.description}</Typography>
                </Grid>
                <Grid md={4} xs={12} className="projectSection">
                    <img alt="Project Picture" className="projectImage" src={this.props.picture}/>
                </Grid>
                <Grid md={4} xs={12} className="projectSection">
                        <Typography variant="h6">Languages / Tools</Typography>
                        <Typography variant="body1" align="center">{this.props.languages}</Typography>
                    </Grid>
            </Grid>
        )
    }
    gitHubLink()
    {
        if(this.props.github != "")
        {
            return (
                <a href={this.props.github} target="_blank">
                <GitHubIcon className="portIcon"/>
                </a>
            )
        }
        return (
            <br/>
        )
    }
}